<template>
    <div class="addresscard-container">
        <v-card class="mx-auto">
            <v-card-text>
                <p class="display-1 text--primary">{{ address.name === null ? '-' : address.name }}</p>
                <div class="text--primary">
                    {{ address.countryName }}<br>
                    {{ address.postCode }} {{ address.city }}<br>
                    {{ address.street }}<br>
                    VAT {{ address.vat === null || address.vat === '' ? '-' : address.vat }}<br>
                    <span class="addresstype">{{ address.isDefault ? 'Default' : '' }} {{address.type === 1 ? 'Invoice' : 'Delivery'}} Address</span>
                </div>
            </v-card-text>
            <v-card-actions>
                <v-flex class="icon_item">
                    <v-spacer></v-spacer>
                    <v-btn transparent class="edit_icon" v-on:click="editDialog = true">
                        <v-img src="../assets/edit.png" class="addresscard-edit" />
                    </v-btn>
                    <v-btn transparent class="delete_icon"  v-on:click="deleteDialog = true">
                        <v-img src="../assets/delete.png" class="addresscard-del" style="{width: 18px; height: 22px}"/>
                    </v-btn>
                </v-flex>
            </v-card-actions>
        </v-card>

        <v-dialog v-model="deleteDialog" max-width="320px">
            <v-card>
                <v-card-title>Delete address</v-card-title>

                <v-card-text class="delete-address-container" v-if="!deleteAddressFail.visible && !deleteAddressSuccess.visible">
                    <v-container grid-list-md>
                        <v-layout wrap>
                            Are you sure you want delete this address?
                        </v-layout>
                    </v-container>
                </v-card-text>

                <v-card-text v-if="deleteAddressFail.visible" class="addaddress-result-container">
                    <v-flex class="deladdress-result-img">
                        <v-img src="../assets/unsuccessful.gif"></v-img>
                    </v-flex>
                    <v-text class="deladdress-fail-text">{{deleteAddressFail.text}}</v-text>
                </v-card-text>

                <v-card-text v-if="deleteAddressSuccess.visible" class="addaddress-result-container">
                    <v-flex class="deladdress-result-img">
                        <v-img src="../assets/successful.gif"></v-img>
                    </v-flex>
                    <v-text class="deladdress-success-text">{{deleteAddressSuccess.text}}</v-text>
                </v-card-text>

                <v-card-actions>
                    <v-btn color="blue darken-1" class="close-change-pass-modal" text v-on:click="deleteDialog = false">Close</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn class="del-address-btn" text v-if="!deleteAddressFail.visible && !deleteAddressSuccess.visible" v-on:click="deleteAddress">Delete</v-btn>
                    <v-btn class="del-address-btn" text v-if="deleteAddressFail.visible" v-on:click="deleteAddress">Try Again</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>




        <v-dialog v-model="editDialog" persistent max-width="600px">
            <v-card>
                <v-card-title>
                    <span class="headline">Edit address</span>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap v-if="!editAddressSuccess.visible && !editAddressFail.visible">
                            <v-flex class="addresstype-container">
                                <label>Type of address</label>
                                <v-radio-group v-model="address.type" row>
                                    <v-radio label="Invoice" value="1"></v-radio>
                                    <v-radio label="Delivery" value="2"></v-radio>
                                </v-radio-group>
                            </v-flex>
                            <v-flex xs12>
                                <v-text-field label="Name*" v-model="address.name" required></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-text-field label="Street 1*" v-model="address.street" required></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-text-field label="Street 2*" v-model="address.street1"></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-text-field label="City*" v-model="address.city" required></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-text-field label="Zip Code*" v-model="address.postCode" required></v-text-field>
                            </v-flex>
                            <v-flex xs12>
                                <v-text-field label="Vat(Optional)" v-model="address.vat"></v-text-field>
                            </v-flex>
                            <v-flex xs12>
                                <v-textarea
                                        v-if="address.type != '2'"
                                        label="Additional Information"
                                        hint="Any information that is missing above"
                                        v-model="address.note"
                                ></v-textarea>
                                <v-textarea
                                        v-if="address.type === '2'"
                                        label="Add delivery instructions"
                                        hint="Some additional info and instructions"
                                        v-model="address.note"
                                ></v-textarea>
                            </v-flex>
                            <v-flex xs12>
                                <v-checkbox v-model="address.isDefault" label="Save as Default"></v-checkbox>
                            </v-flex>
                        </v-layout>
                        <v-layout wrap v-if="editAddressFail.visible" class="addaddress-result-container">
                            <v-flex class="addaddress-result-img">
                                <v-img src="../assets/unsuccessful.gif"></v-img>
                            </v-flex>
                            <v-text class="addaddress-fail-text">{{editAddressFail.text}}</v-text>
                        </v-layout>
                        <v-layout wrap v-if="editAddressSuccess.visible" class="addaddress-result-container">
                            <v-flex class="addaddress-result-img">
                                <v-img src="../assets/successful.gif"></v-img>
                            </v-flex>
                            <v-text class="addaddress-success-text">{{editAddressSuccess.text}}</v-text>
                        </v-layout>
                    </v-container>
                    <small v-if="!editAddressSuccess.visible && !editAddressFail.visible">*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                    <v-btn color="blue darken-1" text @click="editDialog = false">Close</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn class="addnewaddress-btn" v-on:click="editAddress" :disabled="!valid" v-if="!editAddressFail.visible && !editAddressSuccess.visible">Save</v-btn>
                    <v-btn class="addnewaddress-btn" text v-on:click="editAddressFail.visible = false" v-if="editAddressFail.visible">Back</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import { AUser } from '../store/actions';

    export default {
        name: "Addresscard",
        props: {
            address: {
                type: Object,
                required: true
            }
        },
        data () {
            return {
                valid: true,
                deleteDialog: false,
                editDialog: false,
                deleteAddressSuccess: {
                    visible: false,
                    text: 'Address was deleted successfully!'
                },
                deleteAddressFail: {
                    visible: false,
                    text: 'Delete address failed!'
                },
                editAddressSuccess: {
                    visible: false,
                    text: 'Address added successfully!'
                },
                editAddressFail: {
                    visible: false,
                    text: 'Please review again all the information and try again!'
                }
            }
        },
        methods: {
            deleteAddress() {
                this.$store.dispatch(AUser.DELETE_ADDRESS, this.address).then(() => {
                    this.deleteAddressFail.visible = false;
                    this.deleteAddressSuccess.visible = true;
                    this.$store.dispatch(AUser.GET_ADDRESSES);
                }).catch(() => {
                    this.deleteAddressFail.visible = true;
                });
            },
            editAddress() {
                this.$store.dispatch(AUser.UPDATE_ADDRESS, this.address).then(() => {
                    this.editAddressSuccess.visible = true;
                    this.$store.dispatch(AUser.GET_ADDRESSES);
                }).catch(() =>
                    this.editAddressFail.visible = true
                )
            }
        }
    }
</script>

<style scoped>
    .addaddress-result-container {
        max-width: 350px;
        margin: 0 auto !important;
    }
    .delete-address-container {
        padding-top: 15px;
    }
    .addresscard-edit {
        width: 20px;
        height: 26px;
    }
    .addresscard-del {
        width: 18px;
        height: 28px;
    }
    .icon_item {
        display: flex;
        justify-content: flex-end;
    }
    .addresscard-container {
        margin-bottom: 30px;
    }
    .v-card__title {
        background: #014e7a;
        color: #FFFFFF;
        flex-direction: column;
    }
    .v-card__title span {
        margin: 0 auto;
    }
    .del-address-btn {
        background-color: #e91e63!important;
        border-color: #e91e63!important;
        color: #FFF !important;
        width: 180px;
    }
    .deladdress-fail-text {
        color: red;
        font-size: 1em;
        text-align: center;
        width: 100%;
        display: block;
    }
    .deladdress-success-text {
        font-size: 1em;
        text-align: center;
        width: 100%;
        display: block;
    }
    .addresstype {
        font-style: italic;
    }
    .addaddress-fail-text {
        color: red;
        font-size: 1em;
        text-align: center;
        width: 100%;
    }
    .addaddress-success-text {
        font-size: 1em;
        text-align: center;
        width: 100%;
    }
</style>

<style>
    .addresscard-edit .v-image__image--cover,
    .addresscard-del .v-image__image--cover {
        background-size: contain;
    }
</style>