<template>
    <div class="myprofile-container">
        <div class="myaccount-card-container-header">
            <v-avatar color="white">
                <v-icon>mdi-account-circle</v-icon>
            </v-avatar>
            My Profile
        </div>
        <v-flex>
            <v-text-field v-model="userData.fullName" label="Full name"></v-text-field>
        </v-flex>
        <v-flex class="myacc-email-container">
            <v-text-field v-model="userData.email" label="Full name"></v-text-field>
            <div v-if="userData.emailConfirmed" class="email-confirmed" :style="{color: 'green'}">confirmed</div>
            <div v-else class="email-confirmed" :style="{color: 'red'}" :on-press="() => { console.log('click'); confirmEmail(); }">unconfirmed</div>
        </v-flex>
        <v-flex class="myacc-gender-container">
            <label>Gender</label>
            <v-radio-group v-model="userData.gender" row>
                <v-radio label="Male" value="0"></v-radio>
                <v-radio label="Female" value="1"></v-radio>
            </v-radio-group>
        </v-flex>
        <v-flex>
            <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :return-value.sync="date"
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
            >
                <template v-slot:activator="{ on }">
                    <v-text-field
                            v-model="userData.dateOfBirth"
                            label="Date of birth"
                            readonly
                            v-on="on"
                    ></v-text-field>
                </template>
                <v-date-picker v-model="userData.dateOfBirth" no-title scrollable>
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                    <v-btn text color="primary" @click="$refs.menu.save(userData.dateOfBirth)">OK</v-btn>
                </v-date-picker>
            </v-menu>
        </v-flex>
        <v-flex>
            <v-select
                    :items="vin_countries"
                    label="Nationality"
            ></v-select>
        </v-flex>
        <v-flex>
            <v-dialog v-model="dialog" persistent max-width="400px">
                <template v-slot:activator="{ on }">
                    <v-btn class="hidden-sm-and-down change-pass-btn" dark v-on="on">Change Password</v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">Change Password</span>
                    </v-card-title>
                    <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap v-if="!passwordChangeFail.visible && !passwordChangeSuccess.visible">
                                <v-form ref="form" v-model="valid" class="change-pass-form">
                                    <v-flex xs12>
                                        <v-text-field
                                                v-model="currentPassword"
                                                :append-icon="secureTextPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                                :rules="[rules.required, rules.min]"
                                                :type="secureTextPassword ? 'password' : 'text'"
                                                name="input-10-1"
                                                label="Old Password*"
                                                hint="At least 8 characters"
                                                counter
                                                @click:append="secureTextPassword = !secureTextPassword"
                                        ></v-text-field>
                                    </v-flex>
                                    <v-flex xs12>
                                        <v-text-field
                                                v-model="newPassword"
                                                :append-icon="secureTextNewPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                                :rules="[rules.required, rules.min, rules.samePassword]"
                                                :type="secureTextNewPassword ? 'password' : 'text'"
                                                name="input-10-1"
                                                label="New Password*"
                                                hint="At least 8 characters"
                                                counter
                                                @click:append="secureTextNewPassword = !secureTextNewPassword"
                                        ></v-text-field>
                                    </v-flex>
                                </v-form>
                            </v-layout>
                            <v-layout wrap v-if="passwordChangeFail.visible">
                                <v-flex class="changepass-result-img">
                                    <v-img src="../assets/unsuccessful.gif"></v-img>
                                </v-flex>
                                <v-text class="changepass-fail-text">{{passwordChangeFail.text}}</v-text>
                            </v-layout>
                            <v-layout wrap v-if="passwordChangeSuccess.visible">
                                <v-flex class="changepass-result-img">
                                    <v-img src="../assets/successful.gif"></v-img>
                                </v-flex>
                                <v-text class="changepass-success-text">{{passwordChangeSuccess.text}}</v-text>
                            </v-layout>
                        </v-container>
                        <small v-if="!passwordChangeFail.visible && !passwordChangeSuccess.visible">*indicates required field</small>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" class="close-change-pass-modal" text @click="dialog = false">Close</v-btn>
                        <v-btn class="submit-change-pass-btn" text v-on:click="changePassword" :disabled="!valid" v-if="!passwordChangeFail.visible && !passwordChangeSuccess.visible">Save</v-btn>
                        <v-btn class="submit-change-pass-btn" text v-on:click="passwordChangeFail.visible = false" v-if="passwordChangeFail.visible">Back</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-flex>
        <v-flex>
            <v-btn class="submit myprofile-submit" v-on:click="() => saveData()">Save changes</v-btn>
        </v-flex>
    </div>
</template>

<script>
    import API from '../services/userApiService';
    import moment from 'moment';
    import {mapGetters, mapActions, mapState} from 'vuex';
    import { AUser } from '../store/actions';
    import { GUser, GCommon } from '../store/getters';

    export default {
        name: "Myprofile",
        data () {
            return {
                phoneCca2: 'US',
                translation: 'eng',
                vin_countries: {},
                countryMap: {},
                countryCode: '255',
                dialog: false,
                countryValue: null,
                maximumDate: moment().subtract(18, 'years').toDate(),
                valid: true,
                currentPassword: '',
                newPassword: '',
                secureTextPassword: true,
                secureTextNewPassword: true,
                passwordChangeSuccess: {
                    visible: false,
                    text: 'Password changed successfully!'
                },
                passwordChangeFail: {
                    visible: false,
                    text: ''
                },
                rules: {
                    required: value => !!value || 'Required.',
                    min: value => value.length >= 8 || 'Min 8 characters',
                    samePassword: value => value != this.currentPassword || 'You cannot pick the same password'
                }
            }
        },
        computed: {
            country () {
                return this.countries[this.countryCca2].value
            },
            dateOfBirth () {
                let date = this.userData.dateOfBirth
                if (date) return moment(date).toDate()
                else return this.maximumDate
            },
            ...mapGetters({
                userData : GUser.GET_USER_DATA,
                countriesList: GCommon.GET_COUNTRIES_LIST
            })
        },
        methods: {
            setDate(newDate) {
                this.userData.dateOfBirth = moment(newDate).toDate();
            },
            getCountries() {
                this.vin_countries = Object.keys(this.countries);

                this.vin_countries.forEach((code) => {
                    let id = this.countries[code].id;
                    this.countryMap[id] = code
                })
            },
            ...mapState({
                countries: state => state.countries
            }),
            ...mapActions({
                getData: AUser.GET_USER_DATA,
                saveData: AUser.SAVE_USER_DATA,
                confirmEmail: AUser.CONFIRM_EMAIL
            }),
            changePassword() {
                let payload = {
                    userName: this.userData.email,
                    oldPassword: this.currentPassword,
                    newPassword: this.newPassword
                };
                API.instance().changePass(payload).then(res => {
                    this.passwordChangeSuccess.visible = true;
                }).catch(err => {
                    this.passwordChangeFail.visible = true;
                    this.passwordChangeFail.text = err.data[0].message;
                })
            }
        },
        created () {
            this.getData();
            this.getCountries();
        }
    }
</script>

<style scoped>
    .myprofile-container {
        width: 320px;
    }
    .v-card__title {
        background: #014e7a;
        color: #FFFFFF;
    }
    .v-card__title span {
        margin: 0 auto;
    }
    .myacc-email-container {
        position: relative;
    }
    .email-confirmed {
        position: absolute;
        top: 0;
        right: 0;
    }
    .v-input--selection-controls {
        margin-top: 0;
    }
    .myacc-gender-container label {
        font-size: 12px;
    }
    input {
        display: block;
        width: 100%;
        font-size: 16px;
        border-bottom: 2px solid #E0E0E0;
        height: 60px;
    }

    label.vfl-label {
        font-size: 1em;
    }

    input[type="text"] {
        margin-bottom: 40px;
    }
    .change-pass-form {
        width: 100%;
    }
    .submit {
        margin-top: 50px;
        background: #024E7B !important;
        color: #FFF !important;
        width: 100%;
        height: 85px !important;
        font-size: 22px;
        text-transform: uppercase;
        border-radius: 5px;
    }
    .change-pass-btn {
        margin-top: 20px;
        width: 100%;
        color: #ffffff;
        background-color: #DEBA65!important;
        border-color: #DEBA65!important;
    }
    .submit-change-pass-btn {
        background: #024E7B !important;
        border-color: #024E7B!important;
        color: #FFF !important;
        width: 180px;
    }
    .submit-change-pass-btn.v-btn--disabled {
        color: #FFF !important;
        opacity: 0.5;
    }
    .close-change-pass-modal {
        width: 195px;
    }
    .changepass-result-img {
        height: 352px;
        width: 352px;
    }
    .changepass-fail-text {
        color: red;
        font-size: 1em;
        text-align: center;
        width: 100%;
    }
    .changepass-success-text {
        font-size: 1em;
        text-align: center;
        width: 100%;
    }
</style>
<style>
    .v-input--selection-controls .v-input__control {
        width: 100%;
    }
    .v-radio {
        flex: auto;
    }
</style>