<template>
    <div class="addressbook-container">
        <div class="myaccount-card-container-header">
            <v-avatar color="white">
                <v-icon>mdi-calendar-text</v-icon>
            </v-avatar>
            Address Book
        </div>

        <AddressCard v-for='address in addressList' :address='address'></AddressCard>

        <div class="addressbook-error" v-if="addressList.length == 0">There are no records</div>

        <v-dialog v-model="addNewAddressDialog" persistent max-width="600px">
            <template v-slot:activator="{ on }">
                <v-btn
                        absolute
                        dark
                        fab
                        top
                        right
                        color="pink"
                        class="addressbook-addnew-btn"
                        v-on="on"
                >
                    <v-icon>mdi-plus</v-icon>
                    ADD NEW ADDRESS
                </v-btn>
            </template>
            <v-card>
                <v-card-title>
                    <span class="headline">Add new address</span>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap v-if="!addAddressSuccess.visible && !addAddressFail.visible">
                            <v-flex class="addresstype-container">
                                <label>Type of address</label>
                                <v-radio-group v-model="newAddress.type" row>
                                    <v-radio label="Invoice" value="1"></v-radio>
                                    <v-radio label="Delivery" value="2"></v-radio>
                                </v-radio-group>
                            </v-flex>
                            <v-flex xs12>
                                <v-text-field label="Name*" v-model="newAddress.name" required></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-text-field label="Street 1*" v-model="newAddress.street" required></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-text-field label="Street 2*" v-model="newAddress.street1"></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-text-field label="City*" v-model="newAddress.city" required></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-text-field label="Zip Code*" v-model="newAddress.postCode" required></v-text-field>
                            </v-flex>
                            <v-flex xs12>
                                <v-text-field label="Vat(Optional)" v-model="newAddress.vat"></v-text-field>
                            </v-flex>
                            <v-flex xs12>
                                <v-textarea
                                    v-if="newAddress.type != '2'"
                                    label="Additional Information"
                                    hint="Any information that is missing above"
                                    v-model="newAddress.note"
                                ></v-textarea>
                                <v-textarea
                                    v-if="newAddress.type === '2'"
                                    label="Add delivery instructions"
                                    hint="Some additional info and instructions"
                                    v-model="newAddress.note"
                                ></v-textarea>
                            </v-flex>
                            <v-flex xs12>
                                <v-checkbox v-model="newAddress.isDefault" label="Save as Default"></v-checkbox>
                            </v-flex>
                        </v-layout>
                        <v-layout wrap v-if="addAddressFail.visible" class="addaddress-result-container">
                            <v-flex class="addaddress-result-img">
                                <v-img src="../assets/unsuccessful.gif"></v-img>
                            </v-flex>
                            <v-text class="addaddress-fail-text">{{addAddressFail.text}}</v-text>
                        </v-layout>
                        <v-layout wrap v-if="addAddressSuccess.visible" class="addaddress-result-container">
                            <v-flex class="addaddress-result-img">
                                <v-img src="../assets/successful.gif"></v-img>
                            </v-flex>
                            <v-text class="addaddress-success-text">{{addAddressSuccess.text}}</v-text>
                        </v-layout>
                    </v-container>
                    <small v-if="!addAddressSuccess.visible && !addAddressFail.visible">*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                    <v-btn color="blue darken-1" text @click="addNewAddressDialog = false">Close</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn class="addnewaddress-btn" v-on:click="addNewAddress" :disabled="!valid" v-if="!addAddressFail.visible && !addAddressSuccess.visible">Save</v-btn>
                    <v-btn class="addnewaddress-btn" text v-on:click="addAddressFail.visible = false" v-if="addAddressFail.visible">Back</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import AddressCard from './Addresscard';
    import {mapGetters} from 'vuex';
    import {AUser, ACommon} from '../store/actions';
    import {GUser, GCommon} from '../store/getters';

    export default {
        name: "Addressbook",
        components: {
            AddressCard
        },
        data() {
            return {
                newAddress: {
                    name: "",
                    type: "",
                    street: "",
                    street1: "",
                    city: "",
                    postCode: "",
                    countryId: 4,
                    vat: "",
                    note: "",
                    isDefault: false
                },
                valid: true,
                addNewAddressDialog: false,
                addAddressSuccess: {
                    visible: false,
                    text: 'Address added successfully!'
                },
                addAddressFail: {
                    visible: false,
                    text: 'Please review again all the information and try again!'
                }
            };
        },
        methods: {
            addNewAddress() {
                this.$store.dispatch(AUser.ADD_ADDRESS, this.newAddress).then(() => {
                    this.addAddressSuccess.visible = true;
                    this.$store.dispatch(AUser.GET_ADDRESSES);
                }).catch(() => {
                    this.addAddressFail.visible = true;
                })
            }
        },
        created () {
            this.$store.dispatch(AUser.GET_ADDRESSES);
            this.$store.dispatch(ACommon.GET_ALL_COUNTRIES);
        },
        computed: {
            ...mapGetters({
                addressList : GUser.GET_ADDRESSES
            }),
        }
    }
</script>

<style scoped>
    .addresstype-container {
        margin-top: 12px;
    }
    .addresstype-container label {
        font-size: 12px;
    }
    .v-input--selection-controls {
        margin-top: 0;
    }
    .addressbook-addnew-btn {
        width: 250px;
        border-radius: 5px;
        margin-top: 45px;
    }
    .addnewaddress-btn {
        background: #024E7B !important;
        border-color: #024E7B!important;
        color: #FFF !important;
        width: 180px;
    }
    .addnewaddress-btn.v-btn--disabled {
        color: #FFF !important;
        opacity: 0.5;
    }
    .v-card__title {
        background: #014e7a;
        color: #FFFFFF;
    }
    .v-card__title span {
        margin: 0 auto;
    }
    .addaddress-result-img {
        height: 352px;
        width: 352px;
    }
    .addaddress-fail-text {
        color: red;
        font-size: 1em;
        text-align: center;
        width: 100%;
    }
    .addaddress-success-text {
        font-size: 1em;
        text-align: center;
        width: 100%;
    }
</style>
<style>
    .v-input--selection-controls .v-input__control {
        width: 100%;
    }
    .v-radio {
        flex: auto;
    }
    .addaddress-result-container .v-responsive.v-image {
        width: 350px;
        height: 350px;
        margin: 0 auto;
    }
</style>